  flex-direction: column;
  align-items: center;
}
#sixth-block .img,
#seventh-block .img{
  margin: 20px 0;
}
#sixth-block .img img,
#seventh-block .img img{
  /* width: 36%; */
  width: 100%;
  height: 100%;
}
#sixth-block .title h2,
#seventh-block .title h2{
